
.material-icons {
    &.md-18 {
        font-size: 18px;
        width: 18px;
    }
    &.md-24 {
        font-size: 24px;
        width: 24px;
    }
    &.md-36 {
        font-size: 36px;
        width: 36px;
    }
    &.md-48 {
        font-size: 48px;
        width: 48px;
    }
    &.md-dark { color: rgba(0, 0, 0, 0.54); }
    &.md-dark.md-inactive { color: rgba(0, 0, 0, 0.26); }
    &.md-light { color: rgba(255, 255, 255, 1); }
    &.md-light.md-inactive { color: rgba(255, 255, 255, 0.3); }
    &.orange600 { color: #FB8C00; }
}